import axios from 'axios';
import endpoints from './endpoints';
import { parseErrorData, createUrlParamsFromFilters } from './utils';


const create = () => {
  const logIn = payload => {
    const { username, password } = payload;
    const url = endpoints.JWT_AUTH;

    const formData = new FormData();
    formData.append("username", username);
    formData.append("password", password);

    return axios({
          method: 'post',
          url: url,
          data: formData,
          headers: { 'content-type': 'multipart/form-data', }
    })
    .then(response => {
        localStorage.setItem('token', response.data.token);
        return response.data
    })
    .catch(err => {
      const error = new Error(err);
      error.data = parseErrorData(err);
      throw error;
    })
    }

  const logOut = () => {
    localStorage.removeItem('token');
  }

  const checkToken = (payload: string) => {
    return axios.post(endpoints.JWT_CHECK_TOKEN, { token: payload })
      .then(response => {
        localStorage.setItem('token', response.data.token);
        return response.data
      })
      .catch(err => {
        const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
      })
  }

  const partialUpdateUser = payload => {
    const { id, nickname } = payload;
    const token = localStorage.getItem('token') || '';
    const url = endpoints.updateUser(id);

    const formData = new FormData();
    nickname && formData.append("nickname", nickname);

    return axios({
                method: 'patch',
                url: url,
                data: formData,
                headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
    })
    .then(response => {
        return response.data
    })
    .catch(err => {
        const error = new Error(err);
        error.data = parseErrorData(err);
        throw error;
    })
}

  return {
    logIn,
    checkToken,
    logOut,
    partialUpdateUser
  }
}

export default {
  create
}