import { createReducer, createActions } from 'reduxsauce'
import Immutable from 'seamless-immutable'

import { getObjectbyId } from '../../components/utils'

/* ------------- Types and Action Creators ------------- */
const { Types, Creators } = createActions({
    getFeedbacksRequest: ['payload'],
    getFeedbacksFail: ['payload'],
    getFeedbacksSuccess: ['payload'],

    createFeedbackRequest: ['payload'],
    createFeedbackFail: ['payload'],
    createFeedbackSuccess: ['payload'],

    updateFeedbackRequest: ['payload'],
    updateFeedbackFail: ['payload'],
    updateFeedbackSuccess: ['payload'],

    deleteFeedbackRequest: ['payload'],
    deleteFeedbackFail: ['payload'],
    deleteFeedbackSuccess: ['payload'],

    createSuggestionRequest: ['payload'],
    createSuggestionFail: ['payload'],
    createSuggestionSuccess: ['payload'],
})

export const FeedbacksTypes = Types
export default Creators


/* ------------- Initial State ------------- */
export const INITIAL_STATE = Immutable({
    listFetching: false,
    eventFetching: false,
    
    feedback_list: [],
    categories: [],
    errorList: null,

    detailFetching: null,
    feedbackDetail: null,
    errorDetail: null,

    errorEvent: null,

    message: '',
    suggMessage: ''
})


/* ------------- Selectors ------------- */
export const FeedbacksSelectors = {
    getFeedbacks: state => state.feedbacks.feedback_list,
    getCategories: state => state.feedbacks.categories,
}


/* ------------- Reducers ------------- */
// Get Feedbacks
export const getFeedbacksRequest = (state, { payload }) => {
    return state.merge({ listFetching: true, feedback_list: [] })
}

export const getFeedbacksSuccess = (state, { payload }) => {
    return state.merge({ listFetching: false, errorList: null, feedback_list: payload })
}

export const getFeedbacksFail = (state, { payload }) => {
    return state.merge({ listFetching: false, errorList:payload, feedback_list: [] })
}

// create Feedbacks
export const createFeedbackRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, })
}

export const createFeedbackSuccess = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorEvent: null, message: payload.message })
}

export const createFeedbackFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorEvent: payload, })
}

// update 
export const updateFeedbackRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, message: '' })
}

export const updateFeedbackSuccess = (state, { payload }) => {
    const feedbackInList = getObjectbyId(state.feedback_list, payload.id)
    const index = state.feedback_list.indexOf(feedbackInList)
    return state.merge({ 
        eventFetching: false,
        errorEvent: null,
        feedback_list: [
            ...state.feedback_list.slice(0, index),
            payload,
            ...state.feedback_list.slice(index + 1)
            ]
        })
}

export const updateFeedbackFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorEvent: payload, message: '' })
}

// delete
export const deleteFeedbackRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, message: '' })
}

export const deleteFeedbackSuccess = (state, { payload }) => {
    const feedbackInList = getObjectbyId(state.feedback_list, payload.id)
    const index = state.feedback_list.indexOf(feedbackInList)
    return state.merge({ 
        eventFetching: false,
        errorEvent: null,
        feedback_list: [
            ...state.feedback_list.slice(0, index),
            ...state.feedback_list.slice(index + 1)
            ]
        })
}

export const deleteFeedbackFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorEvent: payload, suggMessage: '' })
}

// create Suggestion
export const createSuggestionRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, })
}

export const createSuggestionSuccess = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorEvent: null, suggMessage: payload.message })
}

export const createSuggestionFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorEvent: payload, })
}

/* ------------- Hookup Reducers To Types ------------- */

export const reducer = createReducer(INITIAL_STATE, {
    [Types.GET_FEEDBACKS_REQUEST]: getFeedbacksRequest,
    [Types.GET_FEEDBACKS_SUCCESS]: getFeedbacksSuccess,
    [Types.GET_FEEDBACKS_FAIL]: getFeedbacksFail,

    [Types.CREATE_FEEDBACK_REQUEST]: createFeedbackRequest,
    [Types.CREATE_FEEDBACK_SUCCESS]: createFeedbackSuccess,
    [Types.CREATE_FEEDBACK_FAIL]: createFeedbackFail,

    [Types.UPDATE_FEEDBACK_REQUEST]: updateFeedbackRequest,
    [Types.UPDATE_FEEDBACK_SUCCESS]: updateFeedbackSuccess,
    [Types.UPDATE_FEEDBACK_FAIL]: updateFeedbackFail,

    [Types.DELETE_FEEDBACK_REQUEST]: deleteFeedbackRequest,
    [Types.DELETE_FEEDBACK_SUCCESS]: deleteFeedbackSuccess,
    [Types.DELETE_FEEDBACK_FAIL]: deleteFeedbackFail,

    [Types.CREATE_SUGGESTION_REQUEST]: createSuggestionRequest,
    [Types.CREATE_SUGGESTION_SUCCESS]: createSuggestionSuccess,
    [Types.CREATE_SUGGESTION_FAIL]: createSuggestionFail,
})
