import { takeEvery, all, takeLatest } from "redux-saga/effects";

/* -------------- API -------------- */
import AuthApi from "../api/authApi";
import ProductsApi from '../api/productsApi';
import ShopsApi from '../api/shopsApi';
import FeedbacksApi from '../api/feedbacksApi';

/* ------------- Types ------------- */
import { AuthTypes } from "../redux-sauce/auth";
import { ProductsTypes } from '../redux-sauce/products';
import { ShopsTypes } from '../redux-sauce/shops';
import { FeedbacksTypes } from '../redux-sauce/feedbacks';

/* ------------- Sagas ------------- */
import { logIn, logOut, checkToken, partialUpdateUser } from "./authSagas";
import * as productsSaga from './productsSagas';
import * as shopsSaga from './shopsSagas';
import * as feedbacksSaga from './feedbacksSagas';

const authApi = AuthApi.create();
const productsApi = ProductsApi.create();
const shopsApi = ShopsApi.create();
const feedbacksApi = FeedbacksApi.create();

export default function* root() {
  yield all([
    takeEvery(AuthTypes.LOGIN_REQUEST, logIn, authApi),
    takeEvery(AuthTypes.LOGOUT_REQUEST, logOut, authApi),
    takeEvery(AuthTypes.CHECK_TOKEN_REQUEST, checkToken, authApi),
    takeEvery(AuthTypes.PARTIAL_UPDATE_USER_REQUEST, partialUpdateUser, authApi),

    takeEvery(ProductsTypes.GET_PRODUCTS_REQUEST, productsSaga.getProducts, productsApi),
    takeEvery(ProductsTypes.GET_PRODUCT_DETAIL_REQUEST, productsSaga.getProductDetail, productsApi),
    takeEvery(ProductsTypes.GET_CATEGORIES_REQUEST, productsSaga.getCategories, productsApi),

    takeEvery(ShopsTypes.GET_SHOPS_REQUEST, shopsSaga.getShops, shopsApi),
    takeEvery(ShopsTypes.GET_SHOP_DETAIL_REQUEST, shopsSaga.getShopDetail, shopsApi),
    takeEvery(ShopsTypes.CREATE_SHOP_REQUEST, shopsSaga.createShop, shopsApi),
    takeEvery(ShopsTypes.UPDATE_SHOP_REQUEST, shopsSaga.updateShop, shopsApi),
    takeEvery(ShopsTypes.LIKE_SHOP_REQUEST, shopsSaga.likeShop, shopsApi),

    takeEvery(ShopsTypes.CREATE_PRODUCT_REQUEST, shopsSaga.createProduct, productsApi),
    takeEvery(ShopsTypes.PARTIAL_UPDATE_PRODUCT_REQUEST, shopsSaga.partialUpdateProduct, productsApi),
    takeEvery(ShopsTypes.DELETE_PRODUCT_REQUEST, shopsSaga.deleteProduct, productsApi),
    takeEvery(ShopsTypes.ADD_PRODUCT_IMAGE_REQUEST, shopsSaga.addProductImage, productsApi),
    takeEvery(ShopsTypes.DELETE_PRODUCT_IMAGE_REQUEST, shopsSaga.deleteProductImage, productsApi),

    takeEvery(FeedbacksTypes.GET_FEEDBACKS_REQUEST, feedbacksSaga.getFeedbacks, feedbacksApi),
    takeEvery(FeedbacksTypes.CREATE_FEEDBACK_REQUEST, feedbacksSaga.createFeedback, feedbacksApi),
    takeEvery(FeedbacksTypes.UPDATE_FEEDBACK_REQUEST, feedbacksSaga.updateFeedback, feedbacksApi),
    takeEvery(FeedbacksTypes.DELETE_FEEDBACK_REQUEST, feedbacksSaga.deleteFeedback, feedbacksApi),

    takeEvery(FeedbacksTypes.CREATE_SUGGESTION_REQUEST, feedbacksSaga.createSuggestion, feedbacksApi),
  ]);
}
