import React from 'react';
import { Field, reduxForm } from 'redux-form'

import { renderTextField, renderSelectField } from './ShopForms'


function ProductForm (props) {
  const { categories } = props

  return (
    <div>
      <Field
          component={renderTextField}
          name="title"
          label='Название'
          placeholder='Картошка, буузы, баран и тд.'
          className='input-group my-2'
          labelClass='font-12'
        />

      <Field
          component={renderTextField}
          name="price"
          label='Цена в свободной форме.'
          placeholder='30р за штуку, 500р за кг, 10 за голову и тд.'
          className='input-group my-2'
          labelClass='font-12'
        />
      
      <Field 
          name="category" 
          component={renderSelectField}
          labelClass='font-12 '
          formClass='input-group my-2'
          label='Выберите категорию'
         >
            {categories && categories.length > 0 && categories.map(category => 
              <option className='font-10' value={category.id} key={category.id}>{category.ru_name}</option>
              )}
        </Field>
      
      <Field 
        name="description" 
        component={renderTextField} 
        className="form-control font-12 my-2 "
        style={{"height": "130px", "white-space": "pre-wrap"}}
        variant="outlined"
        labelClass={'font-12'}
        multiline={true}
        rowsMax={5}
        label='Описание(необязательно)'
        />

      <Field
        component={renderTextField}
        name="shop_id"
        hidden={true}
      />

      <Field
        component={renderTextField}
        name="id"
        hidden={true}
      />
    </div>
  )
}

export function CreateProductForm (props) {
  const { parentSubmit, pristine, reset, submitting, handleSubmit, initialValues, categories } = props
  return (
    <div className='card card-style mx-1 mb-1 mt-2'>
      <div className='content'>
        <h3 className='font-18 font-500 text-center'>Добавить продукт</h3>
        <form onSubmit={handleSubmit(parentSubmit)}>
          <ProductForm initialValues={initialValues} categories={categories}/>
          <button 
            className='btn btn-m mb-1 mt-4 rounded-s text-uppercase font-900 shadow-s
            bg-mint-light text-wrap'
            type="submit"
            disabled={pristine || submitting}>
            Создать
          </button>
        </form>
      </div>
    </div>
  )
}

const validateProductForm = values => {
  const errors = {}
  const requiredFields = [
    'title',
    'category',
    'price'
  ]
  requiredFields.forEach(field => {
    if (!values[field]) {
      errors[field] = 'Обязательное поле'
    }
  })
  return errors
}

CreateProductForm = reduxForm({
  form: 'createProductForm',
  validate: validateProductForm,
})(CreateProductForm)


export function UpdateProductForm (props) {
  const { parentSubmit, pristine, reset, submitting, handleSubmit, initialValues, categories } = props
  return (
    <form onSubmit={handleSubmit(parentSubmit)}>
      <div className='card card-style mx-1 mb-1 mt-2'>
        <div className='content mb-5'>
          <ProductForm initialValues={initialValues} categories={categories}/>
        </div>
        <div className='content mt-5 '>
          <button 
            className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
            bg-mint-light text-wrap'
            type="submit"
            disabled={pristine || submitting}>
            Изменить
          </button>
          <button className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
            text-wrap' onClick={props.clickEdit} data-id={null}>
              Отмена
          </button>
        </div>
      </div>
    </form>
  )
}

UpdateProductForm = reduxForm({
  form: 'updateProductForm',
  validate: validateProductForm,
})(UpdateProductForm)