import { put, call } from 'redux-saga/effects';

import ShopsActions from '../redux-sauce/shops'
import AuthActions from '../redux-sauce/auth'

export function* getShops(api, action) {
    try {
        let response = yield call(api.getShops, action.payload);
        yield put(ShopsActions.getShopsSuccess(response.results));
    } catch (err) {
        yield put(ShopsActions.getShopsFail(err))
    }
}

export function* getShopDetail(api, action) {
    try {
        let response = yield call(api.getShopDetail, action.payload);
        yield put(ShopsActions.getShopDetailSuccess(response));
    } catch (err) {
        yield put(ShopsActions.getShopDetailFail(err))
    }
}

export function* createShop(api, action) {
    try {
        let response = yield call(api.createShop, action.payload);
        yield put(ShopsActions.createShopSuccess(response));
        yield put(AuthActions.partialUpdateUserSuccess(response));
    } catch (err) {
        yield put(ShopsActions.createShopFail(err))
    }
}

