import { put, call } from 'redux-saga/effects';

import FeedbacksActions from '../redux-sauce/feedbacks'
import ShopsActions from '../redux-sauce/shops'

export function* getFeedbacks(api, action) {
    try {
        let response = yield call(api.getFeedbacks, action.payload);
        yield put(FeedbacksActions.getFeedbacksSuccess(response.results));
    } catch (err) {
        yield put(FeedbacksActions.getFeedbacksFail(err))
    }
}

export function* createFeedback(api, action) {
    try {
        let response = yield call(api.createFeedback, action.payload);
        yield put(FeedbacksActions.createFeedbackSuccess(response));
        if (response.feedback.shop)
            yield put(ShopsActions.addToFeedbacksList(response.feedback.profile));
    } catch (err) {
        yield put(FeedbacksActions.createFeedbackFail(err))
    }
}

export function* updateFeedback(api, action) {
    try {
        let response = yield call(api.updateFeedback, action.payload);
        yield put(FeedbacksActions.updateFeedbackSuccess(response));
    } catch (err) {
        yield put(FeedbacksActions.updateFeedbackFail(err))
    }
}

export function* deleteFeedback(api, action) {
    try {
        let response = yield call(api.deleteFeedback, action.payload);
        yield put(FeedbacksActions.deleteFeedbackSuccess(response));
        if (response.shop)
            yield put(ShopsActions.removeFromFeedbacksList(response.profile));
    } catch (err) {
        yield put(FeedbacksActions.deleteFeedbackFail(err))
    }
}

export function* createSuggestion(api, action) {
    try {
        let response = yield call(api.createSuggestion, action.payload);
        yield put(FeedbacksActions.createSuggestionSuccess(response));
    } catch (err) {
        yield put(FeedbacksActions.createSuggestionFail(err))
    }
}