import axios from 'axios';
import endpoints from './endpoints';
import { parseErrorData, createUrlParamsFromFilters } from './utils';

const create = () => {

    const getFeedbacks = (filters) => {
        const params = createUrlParamsFromFilters(filters);
        return axios.get(endpoints.FEEDBACKS, { params })
        .then(response => response.data)
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const createFeedback = payload => {
        const { shop, rel_feedback, text } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.FEEDBACKS;

        const formData = new FormData();
        formData.append("text", text);
        shop && formData.append("shop", shop);
        rel_feedback && formData.append("rel_feedback", rel_feedback);
        
        return axios({
                    method: 'post',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const updateFeedback = payload => {
        const { id, text } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.feedback(id);
    
        const formData = new FormData();
        text && formData.append("text", text);
    
        return axios({
                    method: 'patch',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const deleteFeedback = id => {
        const token = localStorage.getItem('token') || '';
        const url = endpoints.feedback(id);

        return axios({
                    method: 'delete',
                    url: url,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const createSuggestion = payload => {
        const { text } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.SUGGS;

        const formData = new FormData();
        formData.append("text", text);
        
        return axios({
                    method: 'post',
                    url: url,
                    data: formData,
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    return {
        getFeedbacks,
        createFeedback,
        updateFeedback,
        deleteFeedback,
        createSuggestion
    }
}

export default {
    create
}