import { put, call } from 'redux-saga/effects';

import ProductsActions from '../redux-sauce/products'

export function* getProducts(api, action) {
    try {
        let response = yield call(api.getProducts, action.payload);
        yield put(ProductsActions.getProductsSuccess(response.results));
    } catch (err) {
        yield put(ProductsActions.getProductsFail(err))
    }
}

export function* getProductDetail(api, action) {
    try {
        let response = yield call(api.getProductDetail, action.payload);
        yield put(ProductsActions.getProductDetailSuccess(response));
    } catch (err) {
        yield put(ProductsActions.getProductDetailFail(err))
    }
}

export function* getCategories(api, action) {
    try {
        let response = yield call(api.getCategories, action.payload);
        yield put(ProductsActions.getCategoriesSuccess(response.results));
    } catch (err) {
        yield put(ProductsActions.getCategoriesFail(err))
    }
}