import React, { Component } from 'react';
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom'
import { CircularProgress } from '@material-ui/core';
import { url } from '../redux/api/endpoints';

// actions
import ProductsActions from '../redux/redux-sauce/products';
import ShopsActions from '../redux/redux-sauce/shops';
import FeedbacksActions from '../redux/redux-sauce/feedbacks';
import { SuggestionForm } from '../components/RestForms';


export function CatButtonComp (props) {
  let history = useHistory()
  const { category } = props

  function handleClick() {
    history.push("/category/" + category.name + "/");
  }

  return (
    <div 
      key={category.id}
      className='card-style mx-0 my-1 pt-2'
      onClick={handleClick}
      style={{
        'background': 'white',
        'width': '110px',
        'height': '100px',
        'color': 'white',
        'font-weight': 'bold',
        'font-size': '1.5em',
        'text-align': 'center',
        'word-break': 'break-word',
        'border-radius': '15px',
        'line-height': '12px',
      }}
    >
      <img 
        src={url + '/' +category.images[0].catalog_image}/>
      <p className='mb-0 mt-1 color-mainDark font-13'>
        {category.ru_name}
      </p>
    </div>
  )
}

class MainCats extends Component {
  constructor(props) {
    super(props);  
    this.state = {
    }
    this.createSug = this.createSug.bind(this);
  }
  componentDidMount() {
    this.props.getCategories()
  }
  
  createSug () {
    this.props.createSugg(this.props.state.form.suggestionForm.values)
  }


  render() {
    const { categories, listFetching } = this.props.state.products
    const { suggMessage } = this.props.state.feedbacks
    const { user } = this.props.state.auth
    
    return (
      listFetching
        ? <CircularProgress className='mx-5 my-5 color-black align-middle text-center'/>
        : <div>
            <div>
              {user && <h3 className='my-3 mx-3 color-white'>Привет, {user ? user.nickname : 'dd'}!</h3>}
              <div className="card header-card shape-rounded" style={{"height": "210px"}}>
                <div className="card-overlay bg-mint-light opacity-95"></div>
                <div className="card-overlay dark-mode-tint"></div>
                {/* <div className="card-bg" 
                  style={{"background-image": "url('images/pictures/20s.jpg')"}}></div> */}
              </div>
            </div>
            <div 
              className='mt-2' 
              style={{
                'display': 'flex',
                'flex-flow': 'row wrap',
                'justify-content': 'space-around'
              }}
            >
            {categories && categories.map(category =>
              <CatButtonComp category={category} getShops={this.props.getShops}/>
            )}
            </div>
            <div className="divider-icon divider-margins bg-mint-light my-3">
              <i className="fa font-17 color-mint-light fa-times"></i>
            </div>
            {!suggMessage 
              ? <SuggestionForm parentSubmit={this.createSug}/>
              : <div className="ad-300x50 bg-mint-light">
                  <h6 class="color-white text-center pt-3 font-15 font-700 text-uppercase">
                    {suggMessage}
                  </h6>
                </div>
            }
        </div>
    );
  }
}

const mapStateToProps = (state) => ({
  state: state
})

const mapDispatchToProps = (dispatch) => ({
  getCategories: query => dispatch(ProductsActions.getCategoriesRequest(query)),
  getShops: query => dispatch(ShopsActions.getShopsRequest(query)),
  createSugg: data => dispatch(FeedbacksActions.createSuggestionRequest(data)),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MainCats);
