import { createReducer, createActions } from 'reduxsauce'
import Immutable from 'seamless-immutable'


/* ------------- Types and Action Creators ------------- */
const { Types, Creators } = createActions({
    getProductsRequest: ['payload'],
    getProductsFail: ['payload'],
    getProductsSuccess: ['payload'],

    getProductDetailRequest: ['payload'],
    getProductDetailFail: ['error'],
    getProductDetailSuccess: ['payload'],

    getCategoriesRequest: ['payload'],
    getCategoriesFail: ['payload'],
    getCategoriesSuccess: ['payload'],

})

export const ProductsTypes = Types
export default Creators


/* ------------- Initial State ------------- */
export const INITIAL_STATE = Immutable({
    listFetching: false,
    product_list: [],
    categories: [],
    errorList: null,

    detailFetching: null,
    productDetail: null,
    errorDetail: null,

    message: '',
})


/* ------------- Selectors ------------- */
export const ProductsSelectors = {
    getProducts: state => state.products.product_list,
    getCategories: state => state.products.categories,
}


/* ------------- Reducers ------------- */
// Get products
export const getProductsRequest = (state, { payload }) => {
    return state.merge({ listFetching: true, product_list: [] })
}

export const getProductsSuccess = (state, { payload }) => {
    return state.merge({ listFetching: false, errorList: null, product_list: payload })
}

export const getProductsFail = (state, { payload }) => {
    return state.merge({ listFetching: false, errorList:payload, product_list: [] })
}

// Get one
export const getProductDetailRequest = (state, { payload }) => {
    return state.merge({ detailFetching: true, })
}

export const getProductDetailSuccess = (state, { payload }) => {
    return state.merge({ detailFetching: false, errorDetail: null, productDetail: payload,
         tours_info: payload.tours_info})
}

export const getProductDetailFail = (state, { error }) => {
    return state.merge({ detailFetching: false, errorDetail: error, productDetail: null })
}

// Get categories
export const getCategoriesRequest = (state, { payload }) => {
    return state.merge({ listFetching: true, categories: [] })
}

export const getCategoriesSuccess = (state, { payload }) => {
    return state.merge({ listFetching: false, errorList: null, categories: payload })
}

export const getCategoriesFail = (state, { payload }) => {
    return state.merge({ listFetching: false, errorList:payload, categories: [] })
}


/* ------------- Hookup Reducers To Types ------------- */

export const reducer = createReducer(INITIAL_STATE, {
    [Types.GET_PRODUCTS_REQUEST]: getProductsRequest,
    [Types.GET_PRODUCTS_SUCCESS]: getProductsSuccess,
    [Types.GET_PRODUCTS_FAIL]: getProductsFail,

    [Types.GET_PRODUCT_DETAIL_REQUEST]: getProductDetailRequest,
    [Types.GET_PRODUCT_DETAIL_SUCCESS]: getProductDetailSuccess,
    [Types.GET_PRODUCT_DETAIL_FAIL]: getProductDetailFail,

    [Types.GET_CATEGORIES_REQUEST]: getCategoriesRequest,
    [Types.GET_CATEGORIES_SUCCESS]: getCategoriesSuccess,
    [Types.GET_CATEGORIES_FAIL]: getCategoriesFail,
})
