import React, { Component } from 'react';
import { connect } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom';

import { url } from '../redux/api/endpoints';
// actions
import ShopActions from '../redux/redux-sauce/shops';
import FeedbacksActions from '../redux/redux-sauce/feedbacks';

import { Feedbacks } from '../components/Feedbacks';
import { SocLink } from './ShopCabinet';
import { AllPhotoGallery } from '../components/Photos';
import { CatButtonComp } from './Main';


function CatButton(props) {
  let history = useHistory();
  const { catName, shopId } = useParams();
  const { cat } = props

  const className = 
    'btn btn-s rounded-l font-500 shadow-xs text-nowrap my-1 mx-1'

  const classActive = catName == cat.name 
    ? className + ' bg-mint-light' 
    : className + ' bg-white'

  function handleClick() {
    history.push("/shops/" + shopId + '/products/' + cat.name + '/')
  }

  return (
    <CatButtonComp
      className={classActive}
      cat={cat} 
      handleClick={handleClick} 
    />
  );
}


function CatProductList2(props) {
  const { catName } = useParams();
  const { products } = props
  const lWidth = window.screen.width
  console.log(lWidth)

  let catProducts = []

  if (catName && products.length > 0) {
    for (var i = 0; i < products.length; i++){
      if (products[i].category === catName){
        catProducts = [...catProducts, products[i]]
      }
    }
  }

  return (
      <div className='content'>
        <div className='mb-3'>
          {catProducts.map(product => 
            <div>
              <p className='d-flex mb-0 justify-content-between'>
                <span className='font-17 font-500'>{product.title}</span>
                <span className='mx-1' 
                  style={{flexGrow: '2'}}>
                    <div className="mt-3" style={{borderBottom: '0.09rem dotted'}}></div>
                </span>
                <span className='font-19 font-italic'>{product.price}</span>
              </p>
              {product.description && 
                <p className='mb-0'>
                  {product.description}
                </p>
              }
            </div>
          )}
        </div>
        <AllPhotoGallery products={catProducts} />
      </div>
    )
}


function ViberBlock(props) {
  const { catName } = useParams();
  const { shopDetail } = props
  const viberIconSrc = url + '/images/viber_icon.png'

  let phone = props.shopDetail.phone
  let re = /8{1}/;
  let re_phone = phone.replace(re, '7');

  return (
    <div className='mt-1'>
      <a href={`viber://chat?number=${re_phone}`} 
        className='btn btn-xs mr-2 font-12 rounded-s shadow-s color-mainFont-light'
        >
        Написать <img src={viberIconSrc} width='20px' />
      </a>
      <a href={`viber://forward?text=${url}/shops/${shopDetail.id}/products/${catName}/`}
        className='btn btn-sx font-12 rounded-s shadow-s color-mainFont-light'
        >
        Поделиться <img src={viberIconSrc} width='20px' />
      </a>
    </div>
  )
}

class ShopDetail extends Component {
  constructor(props) {
    super(props);  
    this.state = {
      catProducts: [],
      active_cat: null,
      showFeed: false,
    }
    this.createFeedback = this.createFeedback.bind(this);
    this.showFeedbacks = this.showFeedbacks.bind(this);
    this.editFeedback = this.editFeedback.bind(this);
    this.openFeedbacks = this.openFeedbacks.bind(this);
  }

  componentDidMount() {
    const { shopId } = this.props.match.params
    this.props.getShopDetail(shopId)
  }

  createFeedback () {
    this.props.createFeedback(this.props.state.form.feedbackCreateForm.values)
  }

  showFeedbacks (shop_id) {
    this.props.getFeedbacks({shop: shop_id})
  }

  openFeedbacks (shop_id) {
    this.props.getFeedbacks({shop: shop_id})
    this.setState({...this.state, showFeed: !this.state.showFeed})
  }

  editFeedback () {
    this.props.updateFeedback(this.props.state.form.feedbackEditForm.values)
  }

  render() {
    const { shopDetail } = this.props.state.shops
    const { feedback_list } = this.props.state.feedbacks
    const user_id  = this.props.state.auth.user ? this.props.state.auth.user.id : null
    let likeClass = 'far fa-heart color-red2-light ml-1 font-21'
    if (shopDetail && shopDetail.likes_list.includes(user_id)) 
      likeClass = 'fas fa-heart color-red2-light ml-1 font-21'

    return (
        shopDetail &&
          <div>
            <div className='card card-style my-2 mx-1 pb-2'>
              <div className='content mb-1'>
                <h5 className='float-left'>{shopDetail.name}</h5>
                <p className='float-right mb-0'>
                  <i className={likeClass} aria-hidden="true"
                    onClick={() => this.props.likeShop(shopDetail.id)}></i>
                    {shopDetail.likes_list.length}
                  <a href='#feedbacks' className='text-reset' onClick={() => this.openFeedbacks(shopDetail.id)}>
                    <i className="far fa-comments ml-3 font-21" aria-hidden="true" ></i>
                      {shopDetail.feedbacks_list.length} 
                  </a>
                </p>
                <div className='clearfix'></div>
                <h5 className='font-13 my-0'><i className="fab fa-viber mr-2"></i>{shopDetail.phone}</h5>
                <ViberBlock shopDetail={shopDetail}/>

                {shopDetail.socials && shopDetail.socials.length > 0 &&
                  <div className='my-2'>
                    {shopDetail.socials.map(soc =>
                    <p className='mb-0'>
                      <SocLink soc={soc}/>
                    </p>
                    )}
                  </div>
                }

                <div className='clearfix'></div>

                {shopDetail.description &&
                  <div className='mt-2' style={{whiteSpace: "pre-wrap"}}>
                    {shopDetail.description}
                  </div>
                }
                {shopDetail.delivery &&
                  <div className='' style={{whiteSpace: "pre-wrap"}}>
                    <p className='my-0 font-14 font-600'>Доставка, самовывоз или точки продаж:</p>
                    {shopDetail.delivery ? shopDetail.delivery : 'Нет информации'}
                  </div>
                }
              </div>

            <Feedbacks 
              shop_id={shopDetail.id}
              user_id={user_id}

              createFeedback={this.createFeedback} 
              showFeedbacks={this.showFeedbacks}
              editFeedback={this.editFeedback}
              deleteFeedback={this.props.deleteFeedback}
              feedback_list={feedback_list}
              whoFeedbackList={shopDetail.feedbacks_list}
              addToFeedbacksList={this.props.addToFeedbacksList}
              
              showFeed={this.state.showFeed}
              openFeedbacks={this.openFeedbacks}

              whoLikeList={shopDetail.likes_list}
              likeShop={this.props.likeShop}
              />
            </div>
            <div className='card card-style mx-1' style={{'transform': 'none'}}>
              {shopDetail.categories.length > 1 &&
                <div className='content d-flex justify-content-center overflow-auto flex-wrap'>
                    {shopDetail.categories.map(cat => 
                      <CatButton cat={cat}/>
                    )} 
                </div>  
                }
              <CatProductList2 products={shopDetail.products} />
            </div>
          </div>
    );
  }
}

const mapStateToProps = (state) => ({
  state: state,
  routing: state.routing,
})

const mapDispatchToProps = (dispatch) => ({
  getShopDetail: id => dispatch(ShopActions.getShopDetailRequest(id)),
  likeShop: shop_id => dispatch(ShopActions.likeShopRequest(shop_id)),

  createFeedback: data => dispatch(FeedbacksActions.createFeedbackRequest(data)),
  updateFeedback: data => dispatch(FeedbacksActions.updateFeedbackRequest(data)),
  deleteFeedback: data => dispatch(FeedbacksActions.deleteFeedbackRequest(data)),
  getFeedbacks: data => dispatch(FeedbacksActions.getFeedbacksRequest(data)),
  addToFeedbacksList : user_id => dispatch(ShopActions.addToFeedbacksList(user_id)),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ShopDetail);
