import React from 'react';
import { Field, reduxForm } from 'redux-form'

import { renderTextField, renderSelectField } from './ShopForms'


export function SuggestionForm (props) {
  const { parentSubmit, pristine, reset, submitting, handleSubmit, initialValues } = props
  return (
    <div className="card card-style">
      <div className='content'>
        <h5 className="text-center font-13 font-700 color-mainFont-dark">
          Предложение или заметка об ошибках
        </h5>
        <form onSubmit={handleSubmit(parentSubmit)}>
          <Field name="text" 
            component={renderTextField}
            variant="outlined"
            multiline={true}
            rowsMax={5}
            className="form-control font-11"
            style={{"height": "140px"}}
            // label='Описание продавца(необязательно)'
            placeholder=' Хотели бы что-то изменить? 
             Нашли ошибку? Пожалуйста, напишите!
             Помогите сделать сервис лучше :)
            '
            />
          <button 
            className='btn btn-l rounded-s text-uppercase font-900 shadow-s
            bg-mint-light text-wrap'
            type="submit"
            disabled={pristine || submitting}>
            Отправить
          </button>
        </form>
      </div>
    </div>
  )
}

const validateSuggestionForm = values => {
  const errors = {}
  const requiredFields = [
    'text',
  ]
  requiredFields.forEach(field => {
    if (!values[field]) {
      errors[field] = 'Обязательное поле'
    }
  })
  return errors
}

SuggestionForm = reduxForm({
  form: 'suggestionForm',
  validate: validateSuggestionForm,
})(SuggestionForm)