import React from 'react';
import ReactDOM from 'react-dom';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

import { Provider } from 'react-redux';
import configureStore from './redux/store/';

import ReactGA from 'react-ga';

// Containers
import Main from './containers/Main'
import MainCats  from './containers/MainCats'
import ShopDetail from './containers/ShopDetail'
import ShopCabinet from './containers/ShopCabinet'
import ViberAuthenticationComponent from './containers/ViberLogin'
import Header from './containers/HeaderContainer'

const store = configureStore()

// ReactGA.initialize('G-0Z91C0YP7N');
// ReactGA.pageview(window.location.pathname + window.location.search);
// ReactGA.event({
//   category: 'TestCat',
//   action: 'Prosto hueta'
// });

ReactDOM.render(
  <Provider store={store}>
    <Router>
      <div className='app' id="page" className=''>
        <Header />
        <div className="page-content header-clear">
          <Switch>
            <Route exact path="/" component={MainCats} />
            <Route exact path="/category/:catName/" component={Main} />
            <Route path="/shops/:shopId/products/:catName" component={ShopDetail} />
            <Route path="/shops/my/" component={ShopCabinet} />
            <Route path="/dm/login/v/:token" component={ViberAuthenticationComponent} />
          </Switch>
        </div>
      </div>
    </Router>
  </Provider>, document.getElementById('root')
);