import { createReducer, createActions } from 'reduxsauce'
import Immutable from 'seamless-immutable'

import { getObjectbyId, lodashToggle } from '../../components/utils'


/* ------------- Types and Action Creators ------------- */
const { Types, Creators } = createActions({
    getShopsRequest: ['payload'],
    getShopsFail: ['payload'],
    getShopsSuccess: ['payload'],

    getShopDetailRequest: ['payload'],
    getShopDetailFail: ['error'],
    getShopDetailSuccess: ['payload'],

    getShopOwnRequest: ['payload'],
    getShopOwnFail: ['error'],
    getShopOwnSuccess: ['payload'],

    createShopRequest: ['payload'],
    createShopFail: ['payload'],
    createShopSuccess: ['payload'],

    updateShopRequest: ['payload'],
    updateShopFail: ['payload'],
    updateShopSuccess: ['payload'],

    createProductRequest: ['payload'],
    createProductFail: ['payload'],
    createProductSuccess: ['payload'],

    partialUpdateProductRequest: ['payload'],
    partialUpdateProductFail: ['payload'],
    partialUpdateProductSuccess: ['payload'],

    deleteProductRequest: ['payload'],
    deleteProductFail: ['payload'],
    deleteProductSuccess: ['payload'],

    addProductImageRequest: ['payload'],
    addProductImageFail: ['payload'],
    addProductImageSuccess: ['payload'],

    deleteProductImageRequest: ['payload'],
    deleteProductImageFail: ['payload'],
    deleteProductImageSuccess: ['payload'],

    addToFeedbacksList: ['user_id'],
    removeFromFeedbacksList: ['user_id'],

    likeShopRequest: ['payload'],
    likeShopFail: ['error'],
    likeShopSuccess: ['payload'],
})

export const ShopsTypes = Types
export default Creators


/* ------------- Initial State ------------- */
export const INITIAL_STATE = Immutable({
    listFetching: false,
    shop_list: [],
    errorList: null,

    detailFetching: null,
    shopDetail: null,
    errorDetail: null,

    shopOwn: null,

    eventFetching: null,
    eventError: null,

    message: '',
})


/* ------------- Selectors ------------- */
export const ShopsSelectors = {
    getShops: state => state.shops.shop_list,
}


/* ------------- Reducers ------------- */
// Get
export const getShopsRequest = (state, { payload }) => {
    return state.merge({ listFetching: true, shop_list: [] })
}

export const getShopsSuccess = (state, { payload }) => {
    return state.merge({ listFetching: false, errorList: null, shop_list: payload })
}

export const getShopsFail = (state, { payload }) => {
    return state.merge({ listFetching: false, errorList:payload, shop_list: [] })
}

// Get one
export const getShopDetailRequest = (state, { payload }) => {
    return state.merge({ detailFetching: true, })
}

export const getShopDetailSuccess = (state, { payload }) => {
    return state.merge({ detailFetching: false, errorDetail: null, shopDetail: payload,})
}

export const getShopDetailFail = (state, { error }) => {
    return state.merge({ detailFetching: false, errorDetail: error, shopDetail: null })
}

// Get own
export const getShopOwnRequest = (state, { payload }) => {
    return state.merge({ detailFetching: true, })
}

export const getShopOwnSuccess = (state, { payload }) => {
    return state.merge({ detailFetching: false, errorDetail: null, shopOwn: payload,})
}

export const getShopOwnFail = (state, { error }) => {
    return state.merge({ detailFetching: false, errorDetail: error, shopOwn: null })
}

// create shop
export const createShopRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, message: '' })
}

export const createShopSuccess = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorList: null, message: payload.message, 
        shopOwn: payload.shop })
}

export const createShopFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorList:payload, message: '' })
}

// update shop
export const updateShopRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, message: '' })
}

export const updateShopSuccess = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorList: null, 
        shopOwn: {
            ...state.shopOwn,
            ...payload
        }
    })
}

export const updateShopFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorList: payload, message: '' })
}

// create product
export const createProductRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, message: '' })
}

export const createProductSuccess = (state, { payload }) => {
    return state.merge({ eventFetching: false, eventError: null,  
        shopOwn: {...state.shopOwn, all_products: [payload, ...state.shopOwn.all_products,]} })
}

export const createProductFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, eventError:payload, message: '' })
}

// partial update product
export const partialUpdateProductRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, message: '' })
}

export const partialUpdateProductSuccess = (state, { payload }) => {
    const productInList = getObjectbyId(state.shopOwn.all_products, payload.id)
    const index = state.shopOwn.all_products.indexOf(productInList)
    return state.merge({ 
        eventFetching: false,
        errorList: null,
        message: payload.message, 
        shopOwn: {
            ...state.shopOwn,
            all_products: [
                ...state.shopOwn.all_products.slice(0, index),
                payload,
                ...state.shopOwn.all_products.slice(index + 1)
            ]
        },
        })
}

export const partialUpdateProductFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorList:payload, message: '' })
}

// delete product
export const deleteProductRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, message: '' })
}

export const deleteProductSuccess = (state, { payload }) => {
    const productInList = getObjectbyId(state.shopOwn.all_products, payload.id)
    const index = state.shopOwn.all_products.indexOf(productInList)
    return state.merge({ 
        eventFetching: false,
        errorList: null,
        message: payload.message, 
        shopOwn: {
            ...state.shopOwn,
            all_products: [
                ...state.shopOwn.all_products.slice(0, index),
                ...state.shopOwn.all_products.slice(index + 1)
            ]
        },
        })
}

export const deleteProductFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorList:payload, message: '' })
}

// add product image
export const addProductImageRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, message: '' })
}

export const addProductImageSuccess = (state, { payload }) => {
    const productInList = getObjectbyId(state.shopOwn.all_products, payload.product.id)
    const index = state.shopOwn.all_products.indexOf(productInList)
    return state.merge({ 
        eventFetching: false,
        errorList: null,
        message: payload.message, 
        shopOwn: {
            ...state.shopOwn,
            all_products: [
                ...state.shopOwn.all_products.slice(0, index),
                payload.product,
                ...state.shopOwn.all_products.slice(index + 1)
            ]
        },
        })
}

export const addProductImageFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorList:payload, message: '' })
}

// delete job image
export const deleteProductImageRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, message: '' })
}

export const deleteProductImageSuccess = (state, { payload }) => {
    const productInList = getObjectbyId(state.shopOwn.all_products, payload.product.id)
    const index = state.shopOwn.all_products.indexOf(productInList)
    return state.merge({ 
        eventFetching: false,
        errorList: null,
        message: payload.message, 
        shopOwn: {
            ...state.shopOwn,
            all_products: [
                ...state.shopOwn.all_products.slice(0, index),
                payload.product,
                ...state.shopOwn.all_products.slice(index + 1)
            ]
        },
        })
}

export const deleteProductImageFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, errorList:payload, message: '' })
}

// add profile id to feedback list
export const addToFeedbacksList = (state, { user_id }) => {
    return state.merge({ 
        shopDetail: {
            ...state.shopDetail,
            feedbacks_list: [
                ...state.shopDetail.feedbacks_list,
                user_id
            ]
        } })
}

// add profile id to feedback list
export const removeFromFeedbacksList = (state, { user_id }) => {
    let feedbacks_list = lodashToggle(state.shopDetail.feedbacks_list, user_id)

    return state.merge({ 
        shopDetail: {
            ...state.shopDetail,
            feedbacks_list: feedbacks_list
        } })
}

// like shop
export const likeShopRequest = (state, { payload }) => {
    return state.merge({ eventFetching: true, message: '' })
}

export const likeShopSuccess = (state, { payload }) => {
    return state.merge({ eventFetching: false, eventError: null, 
        shopDetail: {
            ...state.shopDetail,
            likes_list: payload.likes_list
        }
    })
}

export const likeShopFail = (state, { payload }) => {
    return state.merge({ eventFetching: false, eventError: payload, message: '' })
}
/* ------------- Hookup Reducers To Types ------------- */

export const reducer = createReducer(INITIAL_STATE, {
    [Types.GET_SHOPS_REQUEST]: getShopsRequest,
    [Types.GET_SHOPS_SUCCESS]: getShopsSuccess,
    [Types.GET_SHOPS_FAIL]: getShopsFail,

    [Types.GET_SHOP_DETAIL_REQUEST]: getShopDetailRequest,
    [Types.GET_SHOP_DETAIL_SUCCESS]: getShopDetailSuccess,
    [Types.GET_SHOP_DETAIL_FAIL]: getShopDetailFail,

    [Types.GET_SHOP_OWN_REQUEST]: getShopOwnRequest,
    [Types.GET_SHOP_OWN_SUCCESS]: getShopOwnSuccess,
    [Types.GET_SHOP_OWN_FAIL]: getShopOwnFail,

    [Types.CREATE_SHOP_REQUEST]: createShopRequest,
    [Types.CREATE_SHOP_SUCCESS]: createShopSuccess,
    [Types.CREATE_SHOP_FAIL]: createShopFail,

    [Types.UPDATE_SHOP_REQUEST]: updateShopRequest,
    [Types.UPDATE_SHOP_SUCCESS]: updateShopSuccess,
    [Types.UPDATE_SHOP_FAIL]: updateShopFail,

    [Types.CREATE_PRODUCT_REQUEST]: createProductRequest,
    [Types.CREATE_PRODUCT_SUCCESS]: createProductSuccess,
    [Types.CREATE_PRODUCT_FAIL]: createProductFail,

    [Types.PARTIAL_UPDATE_PRODUCT_REQUEST]: partialUpdateProductRequest,
    [Types.PARTIAL_UPDATE_PRODUCT_SUCCESS]: partialUpdateProductSuccess,
    [Types.PARTIAL_UPDATE_PRODUCT_FAIL]: partialUpdateProductFail,

    [Types.DELETE_PRODUCT_REQUEST]: deleteProductRequest,
    [Types.DELETE_PRODUCT_SUCCESS]: deleteProductSuccess,
    [Types.DELETE_PRODUCT_FAIL]: deleteProductFail,

    [Types.ADD_PRODUCT_IMAGE_REQUEST]: addProductImageRequest,
    [Types.ADD_PRODUCT_IMAGE_SUCCESS]: addProductImageSuccess,
    [Types.ADD_PRODUCT_IMAGE_FAIL]: addProductImageFail,

    [Types.DELETE_PRODUCT_IMAGE_REQUEST]: deleteProductImageRequest,
    [Types.DELETE_PRODUCT_IMAGE_SUCCESS]: deleteProductImageSuccess,
    [Types.DELETE_PRODUCT_IMAGE_FAIL]: deleteProductImageFail,

    [Types.ADD_TO_FEEDBACKS_LIST]: addToFeedbacksList,
    [Types.REMOVE_FROM_FEEDBACKS_LIST]: removeFromFeedbacksList,

    [Types.LIKE_SHOP_REQUEST]: likeShopRequest,
    [Types.LIKE_SHOP_SUCCESS]: likeShopSuccess,
    [Types.LIKE_SHOP_FAIL]: likeShopFail,
})
