import React, { Component, useState  } from 'react';
import { Field, FieldArray, reduxForm } from 'redux-form'

import { styled } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField'
import FormControl from '@material-ui/core/FormControl'
import Select from '@material-ui/core/Select'
import InputLabel from '@material-ui/core/InputLabel'
import FormHelperText from '@material-ui/core/FormHelperText'
import InputAdornment from '@material-ui/core/InputAdornment';
import Icon from '@material-ui/core/Icon';


export const renderFromHelper = ({ touched, error }) => {
  if (!(touched && error)) {
    return
  } else {
    return <FormHelperText>{touched && error}</FormHelperText>
  }
}

export const renderTextField = ({
  label,
  placeholder,
  input,
  multiline,
  meta: { touched, invalid, error },
  labelClass,
  formClass,
  ...custom
}) => (
  <TextField
    label={label}
    placeholder={placeholder}
    error={touched && invalid}
    helperText={touched && error}
    {...input}
    {...custom}
    multiline={multiline}
    InputLabelProps={{
      className: labelClass,
    }}
  />
)

const MySelect = styled(Select)({
  verticalAlign: 'baseline',
  fontSize: '0.8rem'
});

export const renderSelectField = ({
  input,
  label,
  formClass,
  labelClass,
  selectClass,
  meta: { touched, error },
  children,
  ...custom
}) => (
  <FormControl error={touched && error} className={formClass} >
    <InputLabel className={labelClass}>{label}</InputLabel>
    <MySelect
      className={selectClass}
      {...input}
      {...custom}
    >
      {children}
    </MySelect>
    {renderFromHelper({ touched, error })}
  </FormControl>
)

export const renderDateTimeField = ({
  label,
  placeholder,
  input,
  meta: { touched, invalid, error },
  labelClass,
  ...custom
}) => (

  <TextField
    label={label}
    type="date"
    error={touched && invalid}
    helperText={touched && error}
    {...input}
    {...custom}
    InputLabelProps={{
      className: labelClass,
      shrink: true 
    }}
  />
)

const renderSocials = ({ fields, meta: { error, submitFailed } }) => (
    <div className=''>
      <button className='btn btn-m mb-2 rounded-s text-uppercase font-900 shadow-s
            bg-blue2 text-wrap' type="button" onClick={() => fields.push({})}>
        Добавить ссылку на соц. сеть
      </button>
      {submitFailed && error && <span>{error}</span>}
  
    {fields.map((member, index) => (
      <div key={index} >
        <Field
          name={`${member}.link_type`}
          type="text"
          formClass='mx-1 align-middle w-25'
          component={renderSelectField}
          
        >
          <option className='font-10' value='vk' >
            vk
          </option>
          <option className='font-10' value='inst'>instagram</option>
          <option className='font-10' value='web'>web</option>
        </Field>
        
        <Field
          name={`${member}.link`}
          type="text"
          component={renderTextField}
          label="ссылка"
          style={{'width':'60%'}}
          placeholder='https://somewebsite.com'
        />
        <span 
          className='btn btn-xxs ml-2 rounded-s font-900 shadow-s border-red2-dark bg-red2-light'
          type="button"
          title="Remove Member"
          onClick={() => fields.remove(index)}>
            X
        </span>
      </div>
    ))}
  </div>
)

const validateShopForm = values => {
  const errors = {}
  const requiredFields = [
    'name',
    'phone',
  ]
  requiredFields.forEach(field => {
    if (!values[field]) {
      errors[field] = 'Обязательное поле'
    }

    if (values[field] && field === 'phone') {
      if (values['phone'][0] !== '8') errors[field] = 'Телефон должен начинаться с 8.'
      if (values['phone'].length != 11) errors[field] = 'Телефон должен содержать 11 цифр.'
    }
  })

  return errors
}

function ShopForm (props) {
  const [delEnabled, setDel] = useState(false);

  return (
    <div className='mb-3'>
      <Field
          component={renderTextField}
          name="name"
          label='Название магазина'
          className='input-group my-2'
          labelClass='font-12'
        />
      
      <Field
          component={renderTextField}
          type='number'
          name="phone"
          label='Телефон(желательно с вайбером)'
          className='input-group my-2'
          placeholder='8XXXXXXXXXX'
          labelClass='font-12'
        />
      
      {props.description &&
        <Field name="description" 
          component={renderTextField}
          variant="outlined"
          multiline={true}
          rowsMax={5}
          className="form-control font-12 my-2"
          style={{"height": "130px", "white-space": "pre-wrap"}}
          label='Описание продавца(необязательно)'
          placeholder='(Необязательно)
            Любое описание.
          '
          />
      }

    {props.delivery &&
      <Field name="delivery" 
        component={renderTextField}
        className="form-control font-12 my-2"
        style={{"height": "130px"}} 
        label='Условия доставки или где забрать? Есть магазины? Напишите их адреса.'
        variant="outlined"
        multiline={true}
        rowsMax={5}
        placeholder='(Необязательно)
            Напишите как у вас с доставкой или самовывозом.
            Возможно ваш товар есть в магазинах, напишите в каких и где они находятся.
        '
        />
    }
      <Field
        component={renderTextField}
        name="profile_id"
        hidden={true}
      />
      <Field
        component={renderTextField}
        name="id"
        hidden={true}
      />

      {props.socials &&
        <FieldArray name="socials" component={renderSocials} />
      }
    </div>
  )
}

export function InitShopForm (props) {
  const { parentSubmit, pristine, reset, submitting, handleSubmit, initialValues } = props
  return (
    <div className='card card-style mx-0 mb-1 mt-2'>
      <div className='content'>
        <form onSubmit={handleSubmit(parentSubmit)}>
          <ShopForm initialValues={initialValues} description={false} delivery={false}/>
          {/* <p className='mb-0'>Дальше можно добавть соц. сети.</p> */}
          <button 
            className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
            bg-blue2 text-wrap'
            type="submit"
            disabled={pristine || submitting}>
            Далее
          </button>
          
        </form>
      </div>
    </div>
  )
}

InitShopForm = reduxForm({
  form: 'initShopForm',
  validate: validateShopForm,
})(InitShopForm)


export function EditShopForm (props) {
  const { parentSubmit, pristine, reset, submitting, handleSubmit, initialValues } = props

  function submitAndClose(){
    props.setEdit(false)
    parentSubmit()
  }

  return (
    <div className='card card-style mx-0 mb-1 mt-2'>
      <div className='content'>
        <form onSubmit={handleSubmit(submitAndClose)}>
          <ShopForm initialValues={initialValues} phone={false} description={true} socials={true}
            delivery={true}/>
          <button 
            className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
            bg-mint-light text-wrap'
            type="submit"
            disabled={pristine || submitting}>
            Сохранить
          </button>
          <button 
            className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
             text-wrap'
            onClick={() => props.setEdit(false)}
            >
            Назад
          </button>
        </form>
      </div>
    </div>
  )
}

EditShopForm = reduxForm({
  form: 'editShopForm',
  validate: validateShopForm,
})(EditShopForm)


export function FeedbackCreateForm (props) {
  const { parentSubmit, pristine, reset, submitting, handleSubmit, initialValues } = props

  return (
    <div className='card mx-0 mt-3 mb-1'>
      <div className='divider mb-3'></div>
      <div className='content my-0'>
        <p className='mb-0'>Оставить  отзыв</p>
        <form onSubmit={handleSubmit(parentSubmit)}>
        <div>
          <Field name="text" component="textarea" className="form-control font-12 my-2"
            style={{"height": "150px"}} label='Текст'
            />
          <Field
            component='number'
            name="shop"
            hidden={true}
          />
        </div>
          <button 
            className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
            bg-blue2 text-wrap'
            type="submit"
            disabled={pristine || submitting}>
            Сохранить
          </button>
        </form>
      </div>
    </div>
  )
}

FeedbackCreateForm = reduxForm({
  form: 'feedbackCreateForm',
  // validate: validateShopForm,
})(FeedbackCreateForm)

export function FeedbackEditForm (props) {
  const { parentSubmit, pristine, reset, submitting, handleSubmit, initialValues, setEditOnOff } = props
  
  function submitAndClose(){
    setEditOnOff(false)
    parentSubmit()
  }

  return (
    <div className='card card-style mx-0 mb-1 mt-2'>
      <div className='content'>
        <form onSubmit={handleSubmit(submitAndClose)}>
        <div>
          <Field name="text" component="textarea" className="form-control font-12 my-2"
            style={{"height": "150px"}} label='Текст'
            />
          <Field
            component='number'
            name="id"
            hidden={true}
          />
        </div>
          <button 
            className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
            bg-blue2 text-wrap'
            type="submit"
            disabled={pristine || submitting}>
            сохранить
          </button>
          <button 
            className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
            bg-blue2 text-wrap'
            onClick={() => setEditOnOff(false)}
          >
            отмена
          </button>
        </form>
      </div>
    </div>
  )
}

FeedbackEditForm = reduxForm({
  form: 'feedbackEditForm',
  // validate: validateShopForm,
})(FeedbackEditForm)