import { put, call } from 'redux-saga/effects';

import AuthActions from '../redux-sauce/auth'
import ShopsActions from '../redux-sauce/shops'


export function * logIn (api, action) {
  try {
    let response = yield call(api.logIn, action.payload)
    yield put(AuthActions.loginSuccess(response))

  } catch (e) {
    yield put(AuthActions.loginFailure(e.message))
    // yield put(Notifications.error({ title: 'Ошибка', message: e.message, position: 'tc' }));
  }
}

export function * logOut (api, action) {
  try {
    yield call(api.logOut)
    yield put(AuthActions.logoutSuccess())
  } catch (e) {
    yield put(AuthActions.logoutFailure(e.message))
    // yield put(Notifications.error({ title: 'Ошибка', message: e.message, position: 'tc' }));
  }
}

export function* checkToken(api, action) {
  const { payload } = action;
  try {
    let response = yield call(api.checkToken, payload);
    yield put(AuthActions.checkTokenSuccess(response));
    yield put(ShopsActions.getShopOwnSuccess(response.shop));
  } catch (e) {
    yield put(AuthActions.checkTokenFail(e.message))
  }
}

export function* partialUpdateUser(api, action) {
  try {
      let response = yield call(api.partialUpdateUser, action.payload);
      yield put(AuthActions.partialUpdateUserSuccess(response));
  } catch (err) {
      yield put(AuthActions.partialUpdateUserFail(err))
  }
}