import axios from 'axios';
import endpoints from './endpoints';
import { parseErrorData, createUrlParamsFromFilters } from './utils';

const create = () => {

    const getCategories = (filters) => {
        const params = createUrlParamsFromFilters(filters);
        return axios.get(endpoints.GET_CATEGORIES, { params })
        .then(response => response.data)
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const getProducts = (filters) => {
        const params = createUrlParamsFromFilters(filters);
        return axios.get(endpoints.GET_PRODUCTS, { params })
        .then(response => response.data)
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const getProductDetail = id => {
        const token = localStorage.getItem('token') || '';
        const url = endpoints.getProductDetail(id);

        return axios({
                    method: 'get',
                    url: url,
                    // headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const createProduct = payload => {
        const { title, price, description, category } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.GET_PRODUCTS;

        const formData = new FormData();
        formData.append("title", title);
        formData.append("price", price);
        formData.append("category", category);
        description && formData.append("description", description);
        
        return axios({
                    method: 'post',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const partialUpdateProduct = payload => {
        const { id, title, description, price, category, active } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.getProductDetail(id);

        const formData = new FormData();
        title && formData.append("title", title);
        description && formData.append("description", description);
        price && formData.append("price", price);
        category && formData.append("category", category);
        active && formData.append("active", active);
        
        return axios({
                    method: 'patch',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const deleteProduct = id => {
        // const { id } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.getProductDetail(id);

        return axios({
                    method: 'delete',
                    url: url,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const addProductImage = payload => {
        const { id, original } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.addProductImage(id);

        const formData = new FormData();
        formData.append("original", original);
        
        return axios({
                    method: 'post',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const deleteProductImage = payload => {
        const { id, image } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.deleteProductImage(id);

        const formData = new FormData();
        formData.append("image", image);
        
        return axios({
                    method: 'post',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    return {
        getProducts,
        getProductDetail,
        getCategories,
        createProduct,
        partialUpdateProduct,
        deleteProduct,
        addProductImage,
        deleteProductImage
    }
}

export default {
    create
}